.home{
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: "Gill Sans", sans-serif;
}

.headerContainer{
    width: auto;
    margin-left: 50px;
}

.headerContainer h1{
    justify-content: center;
    align-items: center;
    font-size: 90px;
    height: 60px;
    font-weight: 500;
    color: white;
}

.headerContainer p{
    padding: 0;
    font: 60px;
    font-weight: lighter;
    color: white;
}

.headerContainer button{
    border: none;
    border-radius: 5px;
    cursor: pointer;
    justify-content: center;
    background-color: rgb(34, 34,34);
    height: 50px;
    width: 180px;
    color: white;
    text-decoration: none;
    font-size: 20px;
}

.headerContainer button:hover{
    background-color: black;
    cursor: pointer;
    transition: 0.3s ease-in-out;
}

@media only screen and (max-width: 600px){
    .home{
        justify-content: center;
        align-items: center;
    }

    .headerContainer{
        margin-left: 0px;
        border-radius: 10px;
        padding: 10px;
        width: 80%;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        color: white;
        background-color: black;
    }

    .headerContainer h1{
        font-size: 40px;
        height: 30px;
        color: white;
    }

    .headerContainer p{
        font-size: 30px;
        color: white;
    }

    .headerContainer button{
        background-color: white;
        color: black;
        margin-bottom: 30px;
    }

    .headerContainer button:hover{
        background-color: white;
        color: black;
    }
}